<template>
<div class="content">

  <!-- 顶部导航栏 -->
  <div class="block">
    <el-row class="block-row">
      <el-tooltip
        class="box-item"
        effect="dark"
        content="返回"
        placement="bottom"
        :hide-after="0"
      >
        <el-icon
          class="icon"
          size="24px"
          color="rgb(137, 137, 137)"
          style="margin-left: 50px"
          @click="backBaseCourseDetail()"
        >
          <Back />
        </el-icon>
      </el-tooltip>
      <div class="title">{{ course.name }}</div>
    </el-row>
  </div>
  <editBtn @click="goEdit()"></editBtn>
  <div class="body">
    <el-tabs class="major-tab">
      <el-tab-pane
        v-model="chosenMajor"
        v-for="major in majorList"
        :key="major.majorId"
        :label="major.majorName"
      >
        <div class="card">
          <span style="color: grey; font-size: 14px; margin-top: 20px"
            >毕业要求指标点</span
          >

          <div
            class="attribute"
            v-for="indicator in major.indicators"
            :key="indicator.id"
          >
            <div class="attribute-detail">
              <div class="detail-num">
                {{ indicator.serialNum }}
              </div>
              <div class="detail-content">
                <div class="name">{{ indicator.name }}</div>
                <div class="desc">{{ indicator.description }}</div>
              </div>
            </div>
            <!-- 支撑方式 -->
            <div style="margin-left: 90px">
              <span style="color: grey; font-size: 14px; "
                >支撑方式</span
              >
              <div class="methods">
                <div
                  v-for="method in indicator.supportMethodVos"
                  :key="method.id"
                >
                  <div class="method-detail">
                    <div class="method-weight">(&nbsp;{{ method.weight }}%&nbsp;)</div>
                    <div class="method-desc" style="margin-left:80px" >{{ method.name }}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</div>

</template>

<script>
import { Back } from "@element-plus/icons-vue";
import { getIndicators, getMajors, getDetailMajor,getPullIndicator } from "@/api/basecourse";
import { getMajorInfo } from "@/api/major";
import editBtn from "@/components/general/editBtn.vue";

export default {
  name: "baseCourseIndicators",
  components: {
    Back,
    editBtn
    
  },
  data() {
    return {
      chosenMajor: "",
      course: {
        name: "",
        detailId: Number,
        departmentId: Number,
        schoolId: Number,
      },
      majorList: [],
      programId: '',
    };
  },
  mounted() {
    this.course.name = this.$store.state.course.courseName;
    this.course.detailId = this.$store.state.course.detailId;
    this.course.departmentId = this.$store.state.currentInfo.departmentId;
    this.course.schoolId = this.$store.state.currentInfo.schoolId;
    this.programId = this.$store.state.major.programId;
    console.log("!",this.programId);

    console.log(this.course.name, this.course.detailId);
    this.checkMajors();
    this.checkPullIndicators()
  },
  methods: {
    backBaseCourseDetail() {
      this.$router.push("/baseCourseDetail");
    },
    goEdit(){
      this.$router.push("/baseCourseIndicatorsEdit");
    },
    //查询对应的专业以及bcdmId
    checkMajors() {
      getMajors(this.course.detailId).then((res) => {
        this.majorList = res.rows;
        console.log("this.majorList:", this.majorList);
        for (let i = 0; i < this.majorList.length; i++) {
          getMajorInfo(this.majorList[i].majorId).then((res) => {
            this.majorList[i].majorName = res.data.majorName;
          });
        }
        this.checkIndicators();
      });
    },
    //查询各专业对应的Indicators指标点
    checkIndicators() {
      for (let i = 0; i < this.majorList.length; i++) {
        getIndicators(
          this.majorList[i].bcdmId,
          this.course.departmentId,
          this.course.schoolId,
          this.programId
        ).then((res) => {
          console.log("getIndicators:", i, res);
          this.majorList[i].indicators = res.data;

          //处理id->serialNum
          for (let j = 0; j < this.majorList[i].indicators.length; j++) {
            let serialNum = [];
            if (this.majorList[i].indicators[j].id.charAt(0) == 0) {
              serialNum[0] = this.majorList[i].indicators[j].id.charAt(1);
            } else {
              serialNum[0] = this.majorList[i].indicators[j].id.substring(0, 2);
            }
            if (this.majorList[i].indicators[j].id.charAt(2) == 0) {
              serialNum[1] = this.majorList[i].indicators[j].id.charAt(3);
            } else {
              serialNum[1] = this.majorList[i].indicators[j].id.substring(2, 4);
            }
            this.majorList[i].indicators[j].serialNum = serialNum.join(".");
          }
        });
      }
    },
    //查询新增指标点列表
    checkPullIndicators(){
      getPullIndicator(this.programId).then((res)=>{
        console.log("getPullIndicator",res);
      })
    }
   
  },
};
</script>

<style scoped>
.content {
  height: 100vh;
  background-color: #f2f2f2;
 
}
.block {
  position: absolute;
  top: 110px;
  left: 0px;
  height: 55px;
  border-bottom: 1px solid rgb(189, 189, 189);
  width: 100%;
}
.icon {
  cursor: pointer;
}
.block-row {
  margin-top: 18px;
}
.title {
  margin-left: 20px;
  font-size: 16px;
}
.divider {
  margin-left: 20px;
  height: 24px;
}
.body {
  display: flex;
  justify-content: center;
}
.major-tab {
  margin-top: 100px;
}
.card {
  display: flex;
  flex-direction: column;
  width: 750px;
  background: white;
  margin-bottom: 20px;
  box-shadow: 0px 1px 3px rgb(164, 163, 163);
  padding: 0 0 50px 20px;
}
:deep().el-tabs__content {
  overflow: visible;
}
.detail-num {
  color: #5c6bc0;
  font-weight: bold;
  margin-left: 25px;
  margin-top: 20px;
  font-size: 24px;
}
.name {
  font-size: 22px;
  font-weight: bold;
  margin: 20px;
  white-space: nowrap;
  margin-left: 30px;
}
.desc {
  margin: 20px;
  margin-left: 30px;
  margin-top: 25px;
}
.attribute-detail {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}
.methods{
  margin-top:20px
}
.method-detail{
  display: flex;
  flex-direction: row;
  margin-top:10px
}
</style>